<template>
  <div class="shopbag">
    <div v-if="shopbagData.length > 0">
      <van-nav-bar
        fixed
        title="购物袋"
        left-text="返回"
        :right-text="isEdit?'完成':'编辑'"
        left-arrow
        @click-left="back"
        @click-right="onClickRight"
      />
    </div>
    <div v-else>
      <van-nav-bar
        fixed
        title="购物袋"
        left-text="返回"
        left-arrow
        @click-left="back"
        @click-right="onClickRight"
      />
    </div>
    <img class="top-img auto-img" src="../../assets/img/banner3.jpg" alt="">
    <div v-if="shopbagData.length > 0">
      <div class="shopbag-box">
        <div class="pro-list">
          <div class="pro-card" v-for="(item,index) in shopbagData" :key="index">
            <van-swipe-cell :disabled="!isEdit">
              <van-cell :border="false">
                <div class="card-check fl">
                  <van-checkbox v-model="item.isCheck" @change="singleSelection"></van-checkbox>
                </div>
                <div class="pro-img fl" @click="goDetails(item.pid)">
                  <img class="auto-img" :src="item.small_img" alt />
                </div>
                <div class="card-info fr">
                  <div class="pro-info">
                    <div class="clearfix">
                      <div class="fl pro-name">{{item.name}}</div>
                      <div class="fl pro-rule">{{item.rule}}</div>
                    </div>
                    <div class="pro-enname">{{item.enname}}</div>
                  </div>
                  <div class="pro-price">
                    <div>￥{{item.price}}</div>
                    <van-stepper
                      v-model="item.count"
                      @change="modifyCount(item)"
                      theme="round"
                      button-size="20"
                      disable-input
                    />
                  </div>
                </div>
              </van-cell>
              <template #right>
                <van-button
                  square
                  text="删除"
                  color="#0C34BA"
                  class="delete-button"
                  @click="deletePro(item.sid)"
                />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
      <div class="shopbag-submit">
        <van-submit-bar
          v-show="!isEdit"
          :price="totalPrices"
          button-color="#0C34BA"
          button-text="提交订单"
          @submit="goPay"
        >
          <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
        </van-submit-bar>
        <van-submit-bar
          v-show="isEdit"
          button-color="#0C34BA"
          button-text="删除所选"
          @submit="deleteSeletePros"
        >
          <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>
    <div v-else>
      <van-empty description="购物袋空空如也,去逛一逛!">
        <van-button round color="#0C34BA" class="bottom-button" @click="goMenu">去逛一逛</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../../assets/css/shopbag.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      allCheck: false,
      count: 1,
      isEdit: false,
      totalPrices: 0,
      shopbagData: [],
    };
  },
  created() {
    this.getShopbagData();
  },
  methods: {
    // 返回按钮
    back() {
      this.$router.go(-1);
    },

    // 编辑按钮
    onClickRight() {
      this.isEdit = !this.isEdit;
      if (this.isEdit) this.$toast("商品向左滑可删除");
    },

    // 获取购物袋数据
    getShopbagData() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isCheck = false;
            });
            this.shopbagData = result.data.result;
            //
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 修改商品数量
    modifyCount(item) {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }
      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });
      // 加载动画
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      // 发送请求
      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          //

          // 修改数量后重新计算总价
          this.allPrice();
          // if (result.data.code == 6000) {
          //   this.$toast(result.data.msg);
          // }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //删除购物袋数据
    removePros(sids) {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }
      //sids: sid的集合，类型：array
      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data,
      });
    },

    // 单个删除商品
    deletePro(sid, index) {
      this.removePros([sid])
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 7000) {
            this.shopbagData.splice(index, 1);
          }

          this.$toast(result.data.msg);

          // 重新计算总价
          this.allPrice();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 多个删除商品
    deleteSeletePros() {
      // 获取要删除的商品sids
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });
      this.removePros(sids)
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 7000) {
            for (let i = this.shopbagData.length - 1; i >= 0; i--) {
              if (this.shopbagData[i].isCheck) {
                this.shopbagData.splice(i, 1);
              }
            }
            this.allPrice();
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 全选按钮
    allSelect() {
      this.shopbagData.map((v) => {
        v.isCheck = this.allCheck;
      });
    },

    // 单选按钮
    singleSelection() {
      this.allPrice();
      for (let i = 0; i < this.shopbagData.length; i++) {
        if (!this.shopbagData[i].isCheck) {
          this.allCheck = false;
          return;
        }
      }
      this.allCheck = true;
    },

    // 计算总价
    allPrice() {
      this.totalPrices = 0;
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          this.totalPrices += v.count * v.price;
        }
      });
      this.totalPrices *= 100;
    },

    // 提交订单按钮
    goPay() {
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });
      // 序列化，数组转为字符串格式
      sids = JSON.stringify(sids);

      if (sids == "[]") {
        this.$toast("请勾择商品");
        return;
      }
      this.$router.push({ name: "Pay", query: { sids } });
    },

    // 前往菜单
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    //点击图片前往商品详情页面
    goDetails(pid) {
      this.$router.push({ name: "Details", query: { pid } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>